.AssociationForm__Icon {
    position: absolute;
    cursor: pointer;
}

.AssociationForm__Icon--right {
    right: 5px;
}

.PatientInfo{
    grid-area: aside;
    background-color: lightskyblue;
    padding: 1rem 1rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.PatientInfo h3{
    text-align: center;
}
.PatientInfo-Name,.PatientInfo-Description,.PatientInfo-History{
    background-color: white;
    padding: .5rem 1rem;
}
.PatientInfo-Name,.PatientInfo-Description{
    margin-bottom: 1rem;
}
.PatientInfo-Description{
    height: 10rem;
}
.PatientInfo-History{
    -webkit-flex: 1 1 100%;
            flex: 1 1 100%;
}


.Sidebar{
    margin-right: 20px;
}

.Sidebar .ui.vertical.menu .Sidebar-Link_active .item {
    background: rgba(0, 0, 0, .05);
    border-radius: 0;
    box-shadow: none;
}

.Sidebar .ui.vertical.menu .Sidebar-Link_active .item:after {
   visibility: visible;
    background-color: #f2f2f2;
}

.Patient{
    display: -webkit-flex;
    display: flex;
}

.NextState{
    min-height: 15rem;
    position: relative;
    border-radius: 4px;
}

.NextState:not(:last-child){
    margin-bottom: 1rem;
}
.NextState-Content{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    height: calc(100% - 2rem);

}
.NextState .States-Heading{
    margin: 0;
}
.NextState-Label{
    position: absolute;
    top: -1rem;
    left: -.8rem;
}

.NewStatus{
    margin-top: 1rem;
    background-color: white;
    padding: .5rem;
}

.NewStatus-Field {
    margin-top: -19px !important;
    padding-right: 0 !important;
}

.Draft-StatusFormContainer {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row nowrap;
            flex-flow: row nowrap;
    -webkit-align-items: center;
            align-items: center;
}
.Draft-PlusButton {
    cursor: pointer;
}
.Draft-StatusForm--Margined {
    margin-left: calc(21px + .25rem) !important;
}

.Draft .ui.form .inline.fields{
    -webkit-align-items: flex-end;
            align-items: flex-end;
}
.States {
    display: -webkit-flex;
    display: flex;
    padding: 0 1rem 1rem;
    -webkit-align-items: flex-start;
            align-items: flex-start;
}

.States > *:not(:last-child) {
    margin-right: 1rem;
}
.States-Wrap {
    height: 100%;
}
 .States-Draft, .States-NextState {
    margin-top: 1rem;
    background-color: #fff;
    padding: 10px;
}

.States-DraftWrap {
    -webkit-flex-shrink: 1;
            flex-shrink: 1;
}
.States-Draft {
    border-radius: 8px;
}

.States-Heading {
    width: 100%;
    text-align: center;
}

.States-NextWrap {
    width: 400px;
}

.States-Next {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: auto;
    overflow-y: scroll;
}

.App {
    display: grid;
    width: 100vw;
    height: calc(100vh -  2px);
    overflow: hidden;
    grid-gap: 20px;
    grid-template-rows: -webkit-max-content 1fr;
    grid-template-rows: max-content 1fr;
    grid-template-columns: 18rem 1fr;
    grid-template-areas: 'header header'
                        'main main';
    background-color: #fafafa;
}
*{
    box-sizing: border-box;
}
.menu{
    grid-area: header;
}

.Main{
    grid-area: main;
    padding: 0 20px 20px;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

